<template>
  <div
    id="app"
    :class="{ modalIsOpen : isModalOpen }"
  >
    <navbar v-if="!currentRouteIsLogin && !appIsLoading"></navbar>
    <main>
      <loading-spinner v-if="!currentRouteIsLogin && appIsLoading"></loading-spinner>
      <transition
        v-else
        name="fade"
        mode="out-in"
      >
        <router-view />
      </transition>
    </main>

    <modal-container v-on:isModalOpen="modalIsOpen" />
  </div>
</template>

<script>
import Navbar from "./components/Navbar.vue";
import ModalContainer from "./components/modal-components/modal-container.vue";
import LoadingSpinner from "./components/LoaderSpinner.vue";
import firebase from "firebase";

export default {
  name: "App",
  components: {
    Navbar,
    ModalContainer,
    LoadingSpinner
  },
  data: function() {
    return {
      isModalOpen: false
    };
  },
  computed: {
    currentRouteIsLogin: function() {
      return this.$route.name == "Login";
    },
    appIsLoading: function() {
      return this.$store.getters.getLoadingStatus;
    }
  },
  methods: {
    modalIsOpen: function(trueOrFalse) {
      this.isModalOpen = trueOrFalse;
    }
  },
  created: function() {
    firebase.auth().onAuthStateChanged(user => {
      this.$store.dispatch("setUser");
      if (user) {
        this.$store.dispatch("fetchAllData").then(() => {
          const currentUserEmail = this.$store.getters.getUser.email;
          const currentUserInUsersList = this.$store.getters.getUsersList.find(
            user => {
              return user.data().email == currentUserEmail;
            }
          );
          if (currentUserInUsersList.data().admin == true) {
            this.$store.commit("setCurrentUserAdmin");
          }
        });
      }
    });
  }
};
</script>

<style lang="scss">
@import "/scss/main.scss";

#app.modalIsOpen {
  overflow: hidden;
}
</style>



