<template>
  <input
    class="input"
    :type="type"
    :placeholder="placeholder"
    v-bind:value="value"
    @input="$emit('input', $event.target.value)"
    @keyup.enter="$emit('enter', $event.target.value)"
    :class="{invalid : invalid}"
  >
</template>

<script>
export default {
  name: "Base-input",
  props: {
    value: "",
    type: {
      default: "text",
      type: String
    },
    placeholder: {
      default: "",
      type: String
    },
    validation: {
      default: false
    }
  },
  computed: {
    invalid: function() {
      if (this.validation == false) return false;

      if (this.validation == "text") {
        return this.checkIfNotText();
      }
    }
  },
  methods: {
    checkIfNotText: function() {
      return this.value == "";
    }
  }
};
</script>

<style lang="scss" scoped>
.invalid {
  outline: 2px dashed red;
}
</style>



