<template>
  <div class="modal-box fullscreen">
    <div class="modal-title">
      <h3>
        <base-icon
          iconRef="checkmark-square"
          position="left"
        />Advies
      </h3>
      <div class="flex-divider"></div>
      <base-button
        type="secondary"
        @click="closeModal"
      >
        <base-icon iconRef="close" />
      </base-button>
    </div>
    <div class="modal-content">
      <div class="modal-content-section">
        <div class="input-header">
          <h4>
            <base-icon
              iconRef="person"
              position="left"
            />{{currentClient.data().clientName}}</h4>
        </div>
        <div class="modal-values">
          <div class="value-group">
            <div class="value">{{currentClient.data().description}}</div>
          </div>
        </div>
      </div>
      <div class="modal-content-section">
        <div class="input-header">
          <h4>
            <base-icon
              iconRef="archive"
              position="left"
            />{{currentProject.data().projectName}}, {{currentProject.data().address.city}}</h4>
        </div>
        <div class="modal-values">
          <div class="value-group">
            <div class="value">{{currentProject.data().description}}</div>
          </div>
        </div>
      </div>
      <div class="modal-content-section">
        <div class="input-header">
          <h4>
            <base-icon
              iconRef="cube"
              position="left"
            />
            {{currentRoom.data().roomName}}
          </h4>
        </div>
        <div class="modal-values">
          <div class="value-group">
            <div class="value">{{currentRoom.data().description}}</div>
          </div>
          <div class="value-row">
            <div class="value-group">
              <div class="label">Ruimtesoort</div>
              <div class="value">{{$advicevalues.advieswaarden[currentRoom.data().type].label}}</div>
            </div>
            <div class="value-group">
              <div class="label">Oppervlakte</div>
              <div class="value">
                {{(currentRoom.data().width * currentRoom.data().length).toFixed(0)}} m<sup>2</sup>
              </div>
            </div>
            <div class="value-group">
              <div class="label">Hoogte</div>
              <div class="value">
                {{currentRoom.data().height.toFixed(2)}} m
              </div>
            </div>
            <div class="value-group">
              <div class="label">Inhoud</div>
              <div class="value">
                {{(currentRoom.data().width * currentRoom.data().length * currentRoom.data().height).toFixed(0)}} m<sup>3</sup>
              </div>
            </div>
            <div class="value-group">
              <div class="label">Interieur</div>
              <div class="value">
                {{this.$interiors.find(obj => {
                return obj.value === this.currentRoom.data().interior;
                }).label}}
              </div>
            </div>
            <div class="value-group">
              <div class="label">Extra wensen</div>
              <div class="value">
                {{this.$wishes.find(obj => {
                return obj.value === this.currentRoom.data().wishes;
                }).label}}
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="modal-content-section">
        <div class="input-header">
          <h4>
            <base-icon
              iconRef="activity"
              position="left"
            />
            Akoestiek</h4>
          <div class="flex-divider"></div>
          <base-input
            v-model="correction"
            type="number"
            class="xs correction"
            step="0.1"
          ></base-input>
          <span>
            <base-icon iconRef="options" />
          </span>
        </div>
        <div class="modal-values">
          <div class="value-group">
            <div class="value">Op basis van {{zeroMeasurement.data().type}}, {{zeroMeasurement.data().label}} d.d. {{zeroMeasurement.data().date}}</div>
          </div>
        </div>
        <div class="measurements-chart-wrapper">
          <measurements-chart
            :data="[contextualAdviceValuesWithoutAverage, currentMeasurementDataWithoutAverage, rt60withProducts]"
            :scaleMaxHeight="currentMeasurementDataGraphMax"
            :height="250"
            :updateOnValueChange="correction"
          ></measurements-chart>
          <acousticsscore-chart
            :acousticsScores="acousticsScores"
            :currentScenarioHasNoProducts="currentScenarioHasNoProducts"
          ></acousticsscore-chart>
        </div>
      </div>
      <div class="advice-ribbon">
        <div class="ribbon-orbs">
          <div class="ribbon-orb current">
            <div>
              <span class="ribbon-value">
                <base-counter :end="currentMeasurementData[6]" />
              </span> s
            </div>
          </div>
          <div class="ribbon-orb current">

            <div>
              <span class="ribbon-label color-red-dark">
                <base-counter
                  :end="acousticsScores[0]"
                  :decimals="1"
                />
              </span><span class="ribbon-sub-label"> / <span class="color-green-dark">10</span></span></div>
          </div>
          <div class="ribbon-orb advice">
            <div>
              <span class="ribbon-value">
                <base-counter :end="contextualAdviceValues[6]" />
              </span> s
            </div>
          </div>
          <div class="ribbon-orb advice">

            <div><span class="ribbon-label color-green-dark">10,0</span><span class="ribbon-sub-label"> / <span class="color-green-dark">10</span></span></div>
          </div>
          <div
            class="ribbon-orb with-products"
            v-show="!currentScenarioHasNoProducts"
          >
            <div>
              <span class="ribbon-value">
                <base-counter :end="rt60withProductsAverage" />
              </span> s
            </div>
          </div>
          <div
            class="ribbon-orb with-products"
            v-show="!currentScenarioHasNoProducts"
          >
            <div>
              <span class="ribbon-label color-primary-dark">
                <base-counter
                  :end="acousticsScores[1]"
                  :decimals="1"
                />
              </span><span class="ribbon-sub-label"> / <span class="color-green-dark">10</span></span></div>
          </div>
          <div
            class="ribbon-orb with-products"
            v-show="!currentScenarioHasNoProducts"
          >
            <div>
              <span class="ribbon-value">
                <base-counter
                  :end="currentScenarioTotalProductsArea"
                  :decimals="2"
                />
              </span>
            </div>
            <div class="ribbon-sub-label"> Prod. m<sup>2</sup></div>
          </div>
        </div>
      </div>
      <div class="scenario-tabs">
        <button
          class="button secondary tab"
          :class="{ active: scenario.active }"
          v-for="(scenario, scenarioIndex) in scenarios"
          :key="scenarioIndex"
          @click.self="setActiveScenario(scenarioIndex)"
        >
          {{scenarios[scenarioIndex].label}}
          <!--
          <base-button
            type="secondary"
            class="inline-button"
            @click="deleteScenario(scenarioIndex)"
          >
            <base-icon
              iconRef="close"
              position="right"
            />
          </base-button>
          -->
        </button>
        <base-button
          type="secondary"
          @click="addScenario"
        >
          <base-icon iconRef="plus" />
        </base-button>
      </div>
      <!-- Producten -->
      <div class="modal-content-section advice-product-cards-wrapper">
        <div class="input-header">
          <h4>Producten</h4>
        </div>
        <div
          class="advice-product-cards"
          v-for="(scenario, scenarioIndex) in scenarios"
          :key="scenarioIndex"
          v-show="scenario.active"
        >
          <!-- Product card -->
          <div
            class="advice-product-card"
            v-for="(product, index) in scenarios[scenarioIndex].addedProducts"
            :key="index"
            :class="{editing : product.editing}"
          >
            <div class="advice-product-card-half advice-product-card-top-half">
              <!-- Product Image -->
              <div class="advice-product-card-image">
                <img
                  :src="product.imgUrl"
                  alt="Rivasono designpaneel"
                >
              </div>
              <!-- Product Category -->
              <div class="advice-product-card-content">
                <div class="value-row">
                  <div class="value-group product-category">
                    <div
                      class="value"
                      v-show="!product.editing"
                    >
                      {{product.category}}
                    </div>
                  </div>
                </div>
                <!-- Product Name -->
                <div class="value-row">
                  <div class="value-group product-name">
                    <div
                      class="value"
                      v-show="!product.editing"
                    >
                      {{product.productName}}
                    </div>
                    <select
                      class="input value"
                      id="0,0"
                      v-bind:value="product.sku"
                      @change="product.sku = $event.target.value; changed(scenarioIndex, index); calculateTotalArea(scenarioIndex, index)"
                      v-show="product.editing"
                    >
                      <option
                        disabled
                        selected
                        hidden
                        value="0"
                      >Kies een product...</option>
                      <optgroup
                        v-for="(category, categoryOptionIndex) in filteredProductsSortedByCategory"
                        :key="categoryOptionIndex"
                        :label="uniqueProductCategories[categoryOptionIndex]"
                      >
                        <option
                          v-for="(product, productOptionIndex) in category"
                          :value="product.sku"
                          :key="productOptionIndex"
                        >{{product.productName}}</option>
                      </optgroup>
                    </select>
                  </div>
                </div>
                <!-- Product Description -->
                <div class="value-row">
                  <div class="value-group product-description">
                    <div
                      class="value"
                      v-show="!product.editing"
                    >
                      {{product.description}}
                    </div>
                  </div>
                </div>
                <!-- Product Sizes, amount and note -->
                <div class="value-row">
                  <div class="value-group product-size">
                    <label class="label">Breedte, hoogte en opp.</label>
                    <div>
                      <span
                        class="value"
                        v-show="!product.editing || product.editing && product.hasFixedSize"
                      >{{product.height}}</span>
                      <input
                        type="number"
                        class="input value"
                        placeholder="0"
                        v-model="product.height"
                        @keyup="calculateArea(scenarioIndex, index); calculateTotalArea(scenarioIndex, index)"
                        v-show="product.editing && !product.hasFixedSize"
                      >
                      <span>m&nbsp;x&nbsp;</span>
                      <span
                        class="value"
                        v-show="!product.editing || product.editing && product.hasFixedSize"
                      >{{product.width}}</span>
                      <input
                        type="number"
                        class="input value"
                        placeholder="0"
                        v-model="product.width"
                        @keyup="calculateArea(scenarioIndex, index); calculateTotalArea(scenarioIndex, index)"
                        v-show="product.editing && !product.hasFixedSize"
                      >
                      <span>m&nbsp;=&nbsp;</span>
                      <span
                        class="value"
                        v-show="!product.editing || product.editing && product.hasFixedSize"
                      >{{product.area}}</span>
                      <input
                        type="number"
                        class="input value"
                        placeholder="0"
                        v-model="product.area"
                        @keyup="calculateTotalArea(scenarioIndex, index)"
                        v-show="product.editing && !product.hasFixedSize"
                      >
                      <span>m<sup>2</sup></span>
                    </div>
                  </div>
                  <div class="value-group product-distance">
                    <label class="label">Afstand</label>
                    <div
                      class="value"
                      v-show="!product.editing"
                    >{{product.distanceLabel}}</div>
                    <select
                      class="input value"
                      v-bind:value="product.distance"
                      @change="product.distance = $event.target.value; product.distanceLabel = $event.target.options[$event.target.selectedIndex].text"
                      v-show="product.editing"
                    >
                      <option value="1">Gemiddeld (2m tot 4m)</option>
                      <option value="0.9">Dichtbij (&lt; 2m)</option>
                      <option value="1.2">Op afstand (4m tot 8m)</option>
                      <option value="1.5">Op grote afstand (&gt; 8m)</option>
                    </select>
                  </div>
                </div>
                <div class="value-row">
                  <div class="value-group product-amount">
                    <label class="label">Aantal
                      <base-button
                        type="secondary"
                        class="inline-button right"
                        @click="product.amount++; calculateTotalArea(scenarioIndex, index)"
                      >
                        <base-icon iconRef="plus" />
                      </base-button>
                      <base-button
                        type="secondary"
                        class="inline-button right"
                        @click="product.amount--; calculateTotalArea(scenarioIndex, index)"
                      >
                        <base-icon iconRef="minus" />
                      </base-button>
                    </label>
                    <div
                      class="flex-row"
                      v-show="!product.editing"
                    >
                      <div><span class="value">{{product.amount}}</span> stuks</div>
                    </div>
                    <input
                      type="number"
                      class="input value"
                      placeholder="0"
                      @keyup="calculateTotalArea(scenarioIndex, index)"
                      v-model="product.amount"
                      v-show="product.editing"
                    >
                  </div>
                  <div class="value-group product-totalarea">
                    <label class="label">Opp. totaal</label>
                    <div class="value">{{product.totalArea}} m<sup>2</sup></div>
                  </div>
                </div>
                <div
                  class="value-row"
                  v-show="product.editing || product.note != ''"
                >
                  <div class="value-group product-note">
                    <label class="label">Opmerking</label>
                    <div
                      class="value"
                      v-show="!product.editing"
                    >{{product.note}}</div>
                    <textarea
                      class="input value"
                      v-model="product.note"
                      v-show="product.editing"
                    ></textarea>
                  </div>
                </div>
              </div>
              <!-- Card Buttons -->
              <div class="product-card-buttons">
                <base-button
                  type="secondary"
                  @click="toggleEdit(scenarioIndex, index)"
                  v-show="!product.editing"
                >
                  <base-icon iconRef="edit" />
                </base-button>
                <base-button
                  type="secondary"
                  @click="duplicateProduct(scenarioIndex, index)"
                  v-show="!product.editing"
                >
                  <base-icon iconRef="copy" />
                </base-button>
                <base-button
                  type="primary"
                  @click="toggleEdit(scenarioIndex, index)"
                  v-show="product.editing"
                >
                  <base-icon iconRef="checkmark" />
                </base-button>
                <base-button
                  type="danger"
                  @click="deleteProduct(scenarioIndex, index)"
                  v-show="product.editing"
                >
                  <base-icon iconRef="trash" />
                </base-button>
              </div>
            </div>
          </div>
          <div
            class="flex"
            v-if="scenarios[scenarioIndex].addedProducts.length === 0"
          >
            <div class="flex-divider"></div>
            <p>Geen producten toegevoegd</p>
            <div class="flex-divider"></div>
          </div>
          <div class="add-product-button-wrapper">
            <base-button
              type="secondary"
              class="add-product-button"
              @click="addProduct(scenarioIndex)"
            >
              <base-icon
                iconRef="plus"
                position="left"
              />Product toevoegen
            </base-button>
          </div>
        </div>
      </div>
      <div class="modal-content-section disclaimer-section">
        <div class="modal-content">
          <p class="disclaimer">De calculaties zijn gebaseerd op akoestische modellen en schattingen voor algemene situaties. Eventuele metingen zijn ad-hoc uitgevoerd, zonder dat alle bouwkundige eigenschappen bekend zijn. De meetresultaten en calculaties zijn bedoeld ter indicatie; er kunnen geen rechten aan worden ontleend.</p>
        </div>
      </div>
    </div>
    <div class="modal-footer">
      <div class="flex-divider"></div>
      <base-button
        type="secondary"
        @click="closeModal"
      >Annuleren</base-button>
      <base-button
        @click="saveAdvice()"
        :disabled="savingAdvice"
        :loading="savingAdvice"
      >
        <base-icon
          iconRef="save"
          position="left"
        />Opslaan
      </base-button>
    </div>
  </div>
</template>

<script>
import Chart from "chart.js";
import ChartDataLabels from "chartjs-plugin-datalabels";

import MeasurementsChart from "../chart-components/measurements-chart";
import AcousticsscoreChart from "../chart-components/acousticsscore-chart";

export default {
  name: "advice-view-modal",
  components: {
    MeasurementsChart,
    AcousticsscoreChart
  },
  data: function() {
    return {
      savingAdvice: false,
      constant: 0.161,
      correction: 1,
      measurementsChart: "",
      averagesChart: "",
      scenarios: [
        {
          active: true,
          label: "Scenario 1",
          addedProducts: []
        }
      ]
    };
  },
  computed: {
    currentClient: function() {
      return this.$store.getters.getClientsList.find(client => {
        return client.id == this.currentProject.data().belongsTo;
      });
    },
    currentProject: function() {
      return this.$store.getters.getProjectsList.find(project => {
        return project.id == this.currentRoom.data().belongsTo;
      });
    },
    currentRoom: function() {
      return this.$store.getters.getRoomsList.find(room => {
        return room.id == this.$route.params.roomId;
      });
    },
    currentRoomMeasurements: function() {
      return this.$store.getters.getMeasurementsList.filter(measurement => {
        return measurement.data().belongsTo == this.currentRoom.id;
      });
    },
    productsList: function() {
      return this.$store.getters.getProductsList;
    },
    filteredProductsList: function() {
      const filteredProductsList = [];
      this.productsList.forEach(product => {
        filteredProductsList.push({
          active: product.field_364,
          productName: product.field_125,
          brand: product.field_126,
          sku: product.field_127,
          category: product.field_365,
          height: product.field_374_raw,
          width: product.field_375_raw,
          ac: [
            product.field_368_raw,
            product.field_369_raw,
            product.field_370_raw,
            product.field_371_raw,
            product.field_372_raw,
            product.field_373_raw
          ],
          description: product.field_366,
          priceEach: product.field_378_raw,
          pricePerM2: product.field_384_raw,
          imgUrl: product.field_385_raw.url
        });
      });
      return filteredProductsList;
    },
    uniqueProductCategories: function() {
      return [...new Set(this.filteredProductsList.map(item => item.category))];
    },
    filteredProductsSortedByCategory: function() {
      const filteredProductsSortedByCategory = [];
      this.uniqueProductCategories.forEach((category, categoryIndex) => {
        filteredProductsSortedByCategory.push(
          this.filteredProductsList.filter(product => {
            return (
              product.category == this.uniqueProductCategories[categoryIndex]
            );
          })
        );
      });

      return filteredProductsSortedByCategory;
    },
    roomArea: function() {
      return this.currentRoom.data().length * this.currentRoom.data().width;
    },
    roomVolume: function() {
      return this.roomArea * this.currentRoom.data().height;
    },
    currentScenarioHasNoProducts: function() {
      let currentScenarioProductsAmount = 0;
      const activeScenario = this.scenarios.find(scenario => {
        return scenario.active == true;
      });
      activeScenario.addedProducts.forEach(() => {
        currentScenarioProductsAmount++;
      });
      return currentScenarioProductsAmount == 0;
    },
    currentScenarioTotalProductsArea: function() {
      let currentScenarioTotalProductsArea = 0;
      const activeScenario = this.scenarios.find(scenario => {
        return scenario.active == true;
      });
      activeScenario.addedProducts.forEach(product => {
        currentScenarioTotalProductsArea += product.totalArea;
      });
      return currentScenarioTotalProductsArea;
    },
    zeroMeasurement: function() {
      return this.currentRoomMeasurements.find(measurement => {
        return (
          measurement.data().type == "Nulmeting" &&
          measurement.data().trashed == false
        );
      });
    },
    currentMeasurementData: function() {
      const zeroMeasurement = this.currentRoomMeasurements
        .find(measurement => {
          return (
            measurement.data().type == "Nulmeting" &&
            measurement.data().trashed == false
          );
        })
        .data();
      const dataArray = [
        zeroMeasurement.hz125,
        zeroMeasurement.hz250,
        zeroMeasurement.hz500,
        zeroMeasurement.hz1000,
        zeroMeasurement.hz2000,
        zeroMeasurement.hz4000
      ];
      const currentRoomMeasurementAverage = dataArray => {
        return this.roundTo(
          ((zeroMeasurement.hz125 +
            zeroMeasurement.hz250 +
            zeroMeasurement.hz500 +
            zeroMeasurement.hz1000 +
            zeroMeasurement.hz2000 +
            zeroMeasurement.hz4000) /
            6 +
            (zeroMeasurement.hz250 +
              zeroMeasurement.hz500 +
              zeroMeasurement.hz1000 +
              zeroMeasurement.hz2000) /
              4) /
            2,
          2
        );
      };
      dataArray.push(currentRoomMeasurementAverage(dataArray));
      return dataArray;
    },
    currentMeasurementDataWithoutAverage: function() {
      const dataArray = [];
      for (let i = 0; i < 6; i++) {
        dataArray.push(this.currentMeasurementData[i]);
      }
      return dataArray;
    },
    currentMeasurementDataGraphMax: function() {
      const measurementsMax = Math.ceil(
        Math.max.apply(Math, this.currentMeasurementData)
      );
      const adviceValuesMax = Math.ceil(
        Math.max.apply(Math, this.contextualAdviceValues)
      );
      if (measurementsMax > adviceValuesMax) {
        return measurementsMax;
      } else {
        return adviceValuesMax;
      }
    },
    currentAdviceValues: function() {
      return this.$advicevalues.advieswaarden[
        this.currentRoom.data().type
      ].getWaarden(this.roomVolume);
    },
    contextualAdviceValues: function() {
      const contextualAdviceValuesArray = [];
      for (let i = 0; i < 7; i++) {
        contextualAdviceValuesArray.push(
          this.roundTo(
            this.currentAdviceValues[i] *
              this.currentRoom.data().interior *
              this.currentRoom.data().wishes *
              this.correction,
            2
          )
        );
      }
      return contextualAdviceValuesArray;
    },
    contextualAdviceValuesWithoutAverage: function() {
      const contextualAdviceValuesArray = [];
      for (let i = 0; i < 6; i++) {
        contextualAdviceValuesArray.push(this.contextualAdviceValues[i]);
      }
      return contextualAdviceValuesArray;
    },
    currentDifferences: function() {
      const differencesArray = [];
      for (let i = 0; i < 7; i++) {
        differencesArray.push(
          this.roundTo(
            this.currentMeasurementData[i] - this.contextualAdviceValues[i],
            2
          )
        );
      }
      return differencesArray;
    },
    currentAbsorbtion: function() {
      const currentAbsorbtionArray = [];
      for (let i = 0; i < 6; i++) {
        const absorbtion =
          (this.constant * this.roomVolume) /
          this.currentMeasurementDataWithoutAverage[i];
        currentAbsorbtionArray.push(absorbtion);
      }
      return currentAbsorbtionArray;
    },
    totalProductSabins: function() {
      const activeArray = this.scenarios.filter(scenario => {
        return scenario.active == true;
      });
      const addedProducts = activeArray[0].addedProducts;
      const productsSabinsSum = [0, 0, 0, 0, 0, 0];
      for (let x = 0; x < 6; x++) {
        productsSabinsSum[x] +=
          (this.constant * this.roomVolume) /
          this.currentMeasurementDataWithoutAverage[x];
      }
      for (let i = 0; i < addedProducts.length; i++) {
        for (let y = 0; y < 6; y++) {
          productsSabinsSum[y] +=
            addedProducts[i].ac[y] *
            addedProducts[i].totalArea *
            addedProducts[i].distance;
        }
      }
      return productsSabinsSum;
    },
    rt60withProducts: function() {
      const rt60withProductsArray = [];
      for (let i = 0; i < 6; i++) {
        rt60withProductsArray.push(
          (this.roomVolume * this.constant) / this.totalProductSabins[i]
        );
      }
      return rt60withProductsArray;
    },
    rt60withProductsAverage: function() {
      return this.roundTo(
        ((this.rt60withProducts[0] +
          this.rt60withProducts[1] +
          this.rt60withProducts[2] +
          this.rt60withProducts[3] +
          this.rt60withProducts[4] +
          this.rt60withProducts[5]) /
          6 +
          (this.rt60withProducts[1] +
            this.rt60withProducts[2] +
            this.rt60withProducts[3] +
            this.rt60withProducts[4]) /
            4) /
          2,
        2
      );
    },
    acousticsScores: function() {
      const percentages = [
        (this.contextualAdviceValues[6] / this.currentMeasurementData[6]) * 100,
        (this.contextualAdviceValues[6] / this.rt60withProductsAverage) * 100
      ];
      const scores = [
        (Math.round(percentages[0]) / 10).toFixed(1),
        (Math.round(percentages[1]) / 10).toFixed(1)
      ];
      if (scores[1] >= 10) {
        scores[1] = 10;
      }
      return scores;
    }
  },
  filters: {
    toTwoDecimals: function(value) {
      if (isNaN(value)) return "NaN";
      return value.toFixed(2);
    }
  },
  methods: {
    toggleEdit: function(scenarioIndex, productIndex) {
      this.scenarios[scenarioIndex].addedProducts[productIndex].editing = !this
        .scenarios[scenarioIndex].addedProducts[productIndex].editing;
    },
    changed: function(scenarioIndex, productIndex) {
      const currentProduct = this.scenarios[scenarioIndex].addedProducts[
        productIndex
      ];
      const selectedProduct = this.filteredProductsList.find(product => {
        return product.sku == currentProduct.sku;
      });
      currentProduct.productName = selectedProduct.productName;
      currentProduct.description = selectedProduct.description;
      currentProduct.category = selectedProduct.category;
      currentProduct.sku = selectedProduct.sku;
      currentProduct.width = selectedProduct.width / 100;
      currentProduct.height = selectedProduct.height / 100;
      currentProduct.area = selectedProduct.area;
      currentProduct.ac = selectedProduct.ac;
      currentProduct.imgUrl = selectedProduct.imgUrl;
      if (selectedProduct.width != 0 && selectedProduct.height != 0) {
        currentProduct.hasFixedSize = true;
        currentProduct.area = currentProduct.width * currentProduct.height;
      } else {
        currentProduct.hasFixedSize = false;
        currentProduct.area = 0;
      }
      if (
        selectedProduct.ac.every(value => {
          return value == 0;
        })
      ) {
        currentProduct.nonAcoustic = true;
      } else {
        currentProduct.nonAcoustic = false;
      }
      if (selectedProduct.imgUrl == undefined) {
        currentProduct.imgUrl =
          "https://dummyimage.com/480x270/ffffff/009ad9.png&text=++Rivasono++";
      }
    },
    calculateArea: function(scenarioIndex, productIndex) {
      const currentProduct = this.scenarios[scenarioIndex].addedProducts[
        productIndex
      ];
      currentProduct.area = this.roundTo(
        Number(currentProduct.height) * Number(currentProduct.width),
        2
      );
    },
    calculateTotalArea: function(scenarioIndex, productIndex) {
      const currentProduct = this.scenarios[scenarioIndex].addedProducts[
        productIndex
      ];
      currentProduct.totalArea = this.roundTo(
        Number(currentProduct.area) * Number(currentProduct.amount),
        2
      );
    },
    addScenario: function() {
      this.scenarios.push({
        active: false,
        label: `Scenario ${this.scenarios.length + 1}`,
        addedProducts: []
      });
      this.setActiveScenario(this.scenarios.length - 1);
    },
    deleteScenario: function(scenarioIndex) {
      this.scenarios.splice(scenarioIndex, 1);
    },
    setActiveScenario: function(scenarioIndex) {
      this.scenarios.forEach(scenario => {
        scenario.active = false;
      });
      this.scenarios[scenarioIndex].active = true;
    },
    addProduct: function(scenarioIndex) {
      this.scenarios[scenarioIndex].addedProducts.push({
        category: "Kies een productcategorie...",
        productName: "Kies een product...",
        sku: "0",
        ac: [0, 0, 0, 0, 0, 0],
        height: "",
        width: "",
        area: "",
        amount: "0",
        distance: 1,
        distanceLabel: "Gemiddeld (2m tot 4m)",
        totalArea: 0,
        note: "",
        hasFixedSize: false,
        imgUrl:
          "https://dummyimage.com/480x270/ffffff/333333.png&text=++afbeelding++",
        editing: true
      });
    },
    deleteProduct: function(scenarioIndex, productIndex) {
      this.scenarios[scenarioIndex].addedProducts.splice(productIndex, 1);
    },
    duplicateProduct: function(scenarioIndex, productIndex) {
      const productToDuplicate = Object.assign(
        {},
        this.scenarios[scenarioIndex].addedProducts[productIndex]
      );
      this.scenarios[scenarioIndex].addedProducts.splice(
        productIndex + 1,
        0,
        productToDuplicate
      );
    },
    loadAdviceIfRoomHasOne: function() {
      if (this.currentRoom.data().hasAdvice === true) {
        this.scenarios = this.currentRoom.data().scenarios;
      }
    },
    saveAdvice: function() {
      this.savingAdvice = true;
      this.$firestore
        .collection("rooms")
        .doc(this.currentRoom.id)
        .set(
          {
            scenarios: this.scenarios,
            hasAdvice: true
          },
          { merge: true }
        )
        .then(() => {
          this.$store.dispatch("fetchRooms").then(() => {
            this.savingAdvice = false;
          });
        })
        .catch(error => {
          this.savingAdvice = false;
          console.log("Error saving advice: ", error);
        });
    },
    roundTo: function(value, sig) {
      return Number(value.toFixed(sig));
    },
    closeModal: function() {
      this.$store.commit("setModalShown", {
        dataType: "advice",
        modalType: "view",
        shown: false
      });
    }
  },
  mounted: function() {
    Chart.defaults.global.defaultFontFamily = "Inter UI";
    Chart.defaults.global.maintainAspectRatio = false;
  },
  created: function() {
    this.loadAdviceIfRoomHasOne();
  }
};
</script>



