<template>
  <div class="modal-box">
    <div class="modal-title">
      <h3>
        <base-icon
          iconRef="person"
          position="left"
        />Nieuwe Klant
      </h3>
      <div class="flex-divider"></div>
      <base-button
        type="secondary"
        @click="closeModal"
      >
        <base-icon iconRef="close" />
      </base-button>
    </div>
    <div class="modal-content">
      <div class="modal-content-section">
        <div class="input-header">
          <h4>Basisgegevens</h4>
        </div>
        <div class="input-container">
          <label
            class="label"
            for
          >Klantnaam</label>
          <base-input
            type="text"
            placeholder="Klantnaam"
            v-model="newClientData.clientName"
          />
        </div>
        <div class="input-container">
          <label
            for
            class="label"
          >Beschrijving</label>
          <textarea
            class="input"
            rows="4"
            placeholder
            v-model="newClientData.description"
          ></textarea>
        </div>
      </div>
      <div class="modal-content-section">
        <div class="input-header">
          <h4>Contactpersoon</h4>
        </div>
        <div class="input-row">
          <div class="input-container">
            <label class="label">Voornaam</label>
            <base-input
              type="text"
              placeholder="Voornaam"
              v-model="newClientData.contact.firstName"
            />
          </div>
          <div class="input-container">
            <label class="label">Achternaam</label>
            <base-input
              type="text"
              placeholder="Achternaam"
              v-model="newClientData.contact.lastName"
            />
          </div>
        </div>
        <div class="input-container">
          <label class="label">Email adres</label>
          <base-input
            type="email"
            placeholder="Email"
            v-model="newClientData.contact.email"
          />
        </div>
        <div class="input-container">
          <label class="label">Telefoonnummer</label>
          <base-input
            type="tel"
            placeholder="0612345678"
            v-model="newClientData.contact.tel"
          />
        </div>
      </div>
    </div>
    <div class="modal-footer">
      <div class="flex-divider"></div>
      <base-button
        @click="closeModal"
        type="secondary"
      >Annuleren</base-button>
      <base-button
        @click="createClient"
        :loading="creatingClient"
        :disabled="creatingClient"
      >
        <base-icon
          iconRef="checkmark"
          position="left"
        />Aanmaken
      </base-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "client-create-modal",
  data: function() {
    return {
      creatingClient: false,
      newClientData: {
        clientName: "",
        description: "",
        contact: {
          firstName: "",
          lastName: "",
          email: "",
          tel: ""
        }
      }
    };
  },
  methods: {
    createClient: function() {
      this.creatingClient = true;
      const newClient = this.$createNewClient(this.newClientData);
      this.$store.dispatch("createClient", newClient).then(() => {
        this.closeModal();
        this.creatingClient = false;
      });
    },
    closeModal: function() {
      this.$store.commit("setModalShown", {
        dataType: "client",
        modalType: "create",
        shown: false
      });
    }
  }
};
</script>



