<template>
  <div class="page-breadcrumbs">
    <div class="page-breadcrumbs-segment" v-for="parent in parents" :key="parent.path">
      <router-link :to="parent[1]">{{parent[0]}}</router-link>
      <base-icon iconRef="arrow-ios-forward" position/>
    </div>
    <div class="page-breadcrumbs-segment">
      <span class="page-breadcrumb-location">{{currentLocation}}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  props: {
    parents: {
      type: Array
    },
    currentLocation: {
      type: String
    }
  },
  data: function() {
    return {};
  }
};
</script>

<style>
</style>



