<template>
  <div class="modal-box">
    <div class="modal-title">
      <h3>
        <base-icon
          iconRef="cube"
          position="left"
        />Bewerk ruimte
      </h3>
      <div class="flex-divider"></div>
      <base-button
        type="secondary"
        @click="closeModal"
      >
        <base-icon iconRef="close" />
      </base-button>
    </div>
    <div class="modal-content">
      <div class="modal-content-section">
        <div class="input-header">
          <h4>Basisgegevens</h4>
        </div>
        <div class="input-container">
          <label
            class="label"
            for
          >Ruimtenaam</label>
          <base-input
            type="text"
            v-model="roomBody.roomName"
          />
        </div>
        <div class="input-container">
          <label
            for
            class="label"
          >Beschrijving</label>
          <textarea
            class="input"
            v-model="roomBody.description"
          ></textarea>
        </div>
      </div>
      <div class="modal-content-section">
        <div class="input-header">
          <h4>Eigenschappen</h4>
        </div>
        <div class="input-container">
          <label class="label">Ruimtesoort</label>
          <select
            class="input"
            v-bind:value="roomBody.type"
            @change="roomBody.type = $event.target.value"
          >
            <option
              v-for="option in roomTypeOptions"
              :key="option.value"
              :value="option.value"
            >{{option.label}}</option>
          </select>
        </div>
        <div class="input-row">
          <div class="input-container">
            <label class="label">Breedte (m)</label>
            <base-input
              type="number"
              placeholder="0"
              v-model="roomBody.width"
            />
          </div>
          <div class="input-container">
            <label class="label">Lengte (m)</label>
            <base-input
              type="number"
              placeholder="0"
              v-model="roomBody.length"
            />
          </div>
          <div class="input-container">
            <label class="label">Hoogte (m)</label>
            <base-input
              type="number"
              placeholder="0"
              v-model="roomBody.height"
            />
          </div>
        </div>
        <div class="input-row">
          <div class="input-container">
            <label class="label">Aantal personen</label>
            <base-input
              type="number"
              placeholder="0"
              v-model="roomBody.people.amount"
            />
          </div>
          <div class="input-container">
            <label class="label">Gedrag personen</label>
            <select
              class="input"
              v-bind:value="roomBody.people.type"
              @change="roomBody.people.type = $event.target.value"
            >
              <option value="0">Stil aanwezig</option>
              <option value="1">Sprekend</option>
            </select>
          </div>
        </div>
        <div class="input-row">
          <div class="input-container">
            <label class="label">Interieur</label>
            <select
              class="input"
              v-bind:value="roomBody.interior"
              @change="roomBody.interior = $event.target.value"
            >
              <option
                v-for="option in $interiors"
                :key="option.value"
                :value="option.value"
              >{{option.label}}</option>
            </select>
          </div>
          <div class="input-container">
            <label class="label">Extra wensen</label>
            <select
              class="input"
              v-bind:value="roomBody.wishes"
              @change="roomBody.wishes = $event.target.value"
            >
              <option
                v-for="option in $wishes"
                :key="option.value"
                :value="option.value"
              >{{option.label}}</option>
            </select>
          </div>
        </div>
      </div>
    </div>
    <div class="modal-footer">
      <base-button
        @click="deleteRoom"
        type="danger"
        :loading="deletingRoom"
        :disabled="deletingRoom || editingRoom"
      >Verwijderen</base-button>
      <div class="flex-divider"></div>
      <base-button
        @click="closeModal"
        type="secondary"
      >Annuleren</base-button>
      <base-button
        @click="editRoom"
        :loading="editingRoom"
        :disabled="editingRoom || deletingRoom"
      >
        <base-icon
          iconRef="checkmark"
          position="left"
        />Bewerk
      </base-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "room-edit-modal",
  data: function() {
    return {
      deletingRoom: false,
      editingRoom: false,
      roomBody: {
        roomName: "",
        description: "",
        height: "",
        width: "",
        length: "",
        type: "",
        interior: "",
        people: {
          type: "",
          amount: ""
        },
        wishes: ""
      }
    };
  },
  computed: {
    roomTypeOptions: function() {
      return this.$advicevalues.getList();
    },
    currentRoom: function() {
      return this.$store.getters.getRoomsList.find(room => {
        return room.id == this.$route.params.roomId;
      });
    },
    currentProject: function() {
      return this.$store.getters.getProjectsList.find(project => {
        return project.id == this.currentRoom.data().belongsTo;
      });
    },
    currentClient: function() {
      return this.$store.getters.getClientsList.find(client => {
        return client.id == this.currentProject.data().belongsTo;
      });
    }
  },
  methods: {
    editRoom: function() {
      this.editingRoom = true;
      const roomData = {
        id: this.$route.params.roomId,
        body: this.$editRoomDataObject(this.roomBody)
      };
      this.$store.dispatch("editRoom", roomData).then(() => {
        this.editingRoom = false;
        this.closeModal();
      });
    },
    deleteRoom: function() {
      this.deletingRoom = true;
      const roomData = {
        id: this.$route.params.roomId,
        body: this.$deleteRoomDataObject()
      };
      this.$store.dispatch("editRoom", roomData).then(() => {
        this.deletingRoom = false;
        this.closeModal();
        this.$router.push(`/klant/${this.currentProject.id}`);
      });
    },
    closeModal: function() {
      this.$store.commit("setModalShown", {
        dataType: "room",
        modalType: "edit",
        shown: false
      });
    }
  },
  mounted: function() {
    this.roomBody = Object.assign({}, this.currentRoom.data());
  }
};
</script>



