import Vue from "vue";
import Vuex from "vuex";
import Firebase from "firebase";

Vue.use(Vuex);

// Firebase configuration
const firebaseConfig = {
  apiKey: "AIzaSyA6X7Kxa7dajDykCMGU4s2EMLLK5j4Agpw",
  authDomain: "sonar-2-prototype.firebaseapp.com",
  databaseURL: "https://sonar-2-prototype.firebaseio.com",
  projectId: "sonar-2-prototype",
  storageBucket: "sonar-2-prototype.appspot.com",
  messagingSenderId: "335223530722"
};

// Initialize firebase
Firebase.initializeApp(firebaseConfig);
Firebase.firestore().settings({ timestampsInSnapshots: true });

export const store = new Vuex.Store({
  state: {
    isAppLoading: true,
    user: {},
    isCurrentUserAdmin: false,
    adminModeOn: false,
    usersList: [],
    clientsList: [],
    projectsList: [],
    roomsList: [],
    measurementsList: [],
    productsList: [],
    productsAmount: 0,
    modals: {
      client: {
        create: {
          shown: false
        },
        edit: {
          shown: false
        }
      },
      project: {
        create: {
          shown: false
        },
        edit: {
          shown: false
        }
      },
      room: {
        create: {
          shown: false
        },
        edit: {
          shown: false
        }
      },
      measurement: {
        create: {
          shown: false
        },
        edit: {
          shown: false
        }
      },
      analysis: {
        view: {
          shown: false
        }
      },
      advice: {
        view: {
          shown: false
        }
      }
    }
  },
  getters: {
    getLoadingStatus: state => {
      return state.isAppLoading;
    },
    getUser: state => {
      return state.user;
    },
    getCurrentUserAdminStatus: state => {
      return state.isCurrentUserAdmin;
    },
    getAdminModeStatus: state => {
      return state.adminModeOn;
    },
    getClientsList: state => {
      return state.clientsList;
    },
    getProjectsList: state => {
      return state.projectsList;
    },
    getRoomsList: state => {
      return state.roomsList;
    },
    getMeasurementsList: state => {
      return state.measurementsList;
    },
    getUsersList: state => {
      return state.usersList;
    },
    getENVList: state => {
      return state.ENVList;
    },
    getProductsList: state => {
      return state.productsList;
    },
    getModalStates: state => {
      return state.modals;
    }
  },
  mutations: {
    setAppLoading: (state, trueOrFalse) => {
      state.isAppLoading = trueOrFalse;
    },
    setUser: state => {
      state.user = Firebase.auth().currentUser;
    },
    setCurrentUserAdmin: state => {
      state.isCurrentUserAdmin = true;
    },
    toggleAdminMode: state => {
      state.adminModeOn = !state.adminModeOn;
    },
    setClients: (state, clientsList) => {
      state.clientsList = clientsList;
    },
    setProjects: (state, projectsList) => {
      state.projectsList = projectsList;
    },
    setRooms: (state, roomsList) => {
      state.roomsList = roomsList;
    },
    setMeasurements: (state, measurementsList) => {
      state.measurementsList = measurementsList;
    },
    setUsers: (state, usersList) => {
      state.usersList = usersList;
    },
    setENV: (state, ENVList) => {
      state.ENVList = ENVList;
    },
    setProducts: (state, productsListAndAmount) => {
      state.productsList = productsListAndAmount[0];
      state.productsAmount = productsListAndAmount[1];
    },
    setModalShown: (state, modalData) => {
      state.modals[modalData.dataType][modalData.modalType].shown = modalData.shown;
    }
  },
  actions: {
    setUser: ({ commit }) => {
      commit("setUser");
    },
    // Fetching actions
    fetchAllData: ({ commit, dispatch }) => {
      return new Promise((resolve, reject) => {
        commit("setAppLoading", true);
        const fetchPromises = [
          dispatch("fetchClients"),
          dispatch("fetchProjects"),
          dispatch("fetchRooms"),
          dispatch("fetchMeasurements"),
          dispatch("fetchUsers"),
          dispatch("fetchENVAndProducts")
        ];
        Promise.all(fetchPromises)
          .then(() => {
            console.log("All fetch promises resolved");
            commit("setAppLoading", false);
            resolve();
          })
          .catch(error => {
            console.log("Error fetching data: ", error);
            reject();
          });
      });
    },
    fetchClients: ({ commit }) => {
      return new Promise((resolve, reject) => {
        Firebase.firestore()
          .collection("clients")
          .get()
          .then(querySnapshot => {
            const clientsList = [];
            querySnapshot.forEach(doc => {
              clientsList.push(doc);
            });
            commit("setClients", clientsList);
          })
          .then(() => {
            console.log("Done fetching clients");
            resolve();
          })
          .catch(error => {
            console.log("Error fetching clients:", error);
            reject(error);
          });
      });
    },
    fetchProjects: ({ commit }) => {
      return new Promise((resolve, reject) => {
        console.log("Started fetching Projects");
        Firebase.firestore()
          .collection("projects")
          .get()
          .then(querySnapshot => {
            const projectsList = [];
            querySnapshot.forEach(doc => {
              projectsList.push(doc);
            });
            commit("setProjects", projectsList);
          })
          .then(() => {
            console.log("Done fetching projects");
            resolve();
          })
          .catch(error => {
            console.log("Error fetching projects:", error);
            reject(error);
          });
      });
    },
    fetchRooms: ({ commit }) => {
      return new Promise((resolve, reject) => {
        Firebase.firestore()
          .collection("rooms")
          .get()
          .then(querySnapshot => {
            const roomsList = [];
            querySnapshot.forEach(doc => {
              roomsList.push(doc);
            });
            commit("setRooms", roomsList);
          })
          .then(() => {
            console.log("Done fetching rooms");
            resolve();
          })
          .catch(error => {
            console.log("Error fetching rooms:", error);
            reject(error);
          });
      });
    },
    fetchMeasurements: ({ commit }) => {
      return new Promise((resolve, reject) => {
        Firebase.firestore()
          .collection("measurements")
          .get()
          .then(querySnapshot => {
            const measurementsList = [];
            querySnapshot.forEach(doc => {
              measurementsList.push(doc);
            });
            commit("setMeasurements", measurementsList);
          })
          .then(() => {
            console.log("Done fetching measurements");
            resolve();
          })
          .catch(error => {
            console.log("Error fetching measurements:", error);
            reject(error);
          });
      });
    },
    fetchUsers: ({ commit }) => {
      return new Promise((resolve, reject) => {
        Firebase.firestore()
          .collection("users")
          .get()
          .then(querySnapshot => {
            const usersList = [];
            querySnapshot.forEach(doc => {
              usersList.push(doc);
            });
            commit("setUsers", usersList);
          })
          .then(() => {
            console.log("Done fetching users");
            resolve();
          })
          .catch(error => {
            console.log("Error fetching users:", error);
            reject(error);
          });
      });
    },
    fetchENVAndProducts: ({ commit, state }) => {
      return new Promise((resolve, reject) => {
        Firebase.firestore()
          .collection("ENV")
          .get()
          .then(querySnapshot => {
            const ENVList = [];
            querySnapshot.forEach(doc => {
              ENVList.push(doc);
            });
            commit("setENV", ENVList);
          })
          .then(() => {
            console.log("Done fetching ENV");
            const url = "https://api.knack.com/v1/objects/object_2/records?rows_per_page=1000";
            fetch(url, {
              headers: {
                "X-Knack-REST-API-KEY": state.ENVList[0].data().value,
                "X-Knack-Application-Id": state.ENVList[1].data().value
              }
            })
              .then(knackResponse => {
                return knackResponse.json();
              })
              .then(value => {
                const productsList = value.records;
                const productsAmount = value.total_records;
                commit("setProducts", [productsList, productsAmount]);
                console.log("Done fetching products");
                resolve();
              })
              .catch(error => {
                console.log("Error fetching products");
                reject(error);
              });
          })
          .catch(error => {
            console.log("Error fetching ENV:", error);
            reject(error);
          });
      });
    },
    // Creating actions
    createClient: ({ dispatch }, newClient) => {
      return new Promise((resolve, reject) => {
        Firebase.firestore()
          .collection("clients")
          .add(newClient)
          .then(() => {
            console.log("Done creating client");
            dispatch("fetchClients").then(() => {
              resolve();
            });
          })
          .catch(error => {
            console.log("Error creating client: ", error);
            reject();
          });
      });
    },
    createProject: ({ dispatch }, newProject) => {
      return new Promise((resolve, reject) => {
        Firebase.firestore()
          .collection("projects")
          .add(newProject)
          .then(() => {
            console.log("Done creating project");
            dispatch("fetchProjects").then(() => {
              resolve();
            });
          })
          .catch(error => {
            console.log("Error creating project: ", error);
            reject();
          });
      });
    },
    createRoom: ({ dispatch }, newRoom) => {
      return new Promise((resolve, reject) => {
        Firebase.firestore()
          .collection("rooms")
          .add(newRoom)
          .then(() => {
            console.log("Done creating room");
            dispatch("fetchRooms").then(() => {
              resolve();
            });
          })
          .catch(error => {
            console.log("Error creating room: ", error);
            reject();
          });
      });
    },
    createMeasurement: ({ dispatch }, newMeasurement) => {
      return new Promise((resolve, reject) => {
        Firebase.firestore()
          .collection("measurements")
          .add(newMeasurement)
          .then(() => {
            dispatch("fetchMeasurements").then(() => {
              console.log("Done creating measurement");
              resolve();
            });
          })
          .catch(error => {
            console.log("Error creating measurement: ", error);
            reject();
          });
      });
    },
    // Editing actions
    editClient: ({ dispatch }, clientData) => {
      return new Promise((resolve, reject) => {
        Firebase.firestore()
          .collection("clients")
          .doc(clientData.id)
          .set(clientData.body, { merge: true })
          .then(() => {
            console.log("Done editing client");
            dispatch("fetchClients").then(() => {
              resolve();
            });
          })
          .catch(error => {
            console.log("Error editing client: ", error);
            reject();
          });
      });
    },
    editProject: ({ dispatch }, projectData) => {
      return new Promise((resolve, reject) => {
        Firebase.firestore()
          .collection("projects")
          .doc(projectData.id)
          .set(projectData.body, { merge: true })
          .then(() => {
            console.log("Done editing project");
            dispatch("fetchProjects").then(() => {
              resolve();
            });
          })
          .catch(error => {
            console.log("Error editing project: ", error);
            reject();
          });
      });
    },
    editRoom: ({ dispatch }, roomData) => {
      return new Promise((resolve, reject) => {
        Firebase.firestore()
          .collection("rooms")
          .doc(roomData.id)
          .set(roomData.body, { merge: true })
          .then(() => {
            console.log("Done editing room");
            dispatch("fetchRooms").then(() => {
              resolve();
            });
          })
          .catch(error => {
            console.log("Error editing room: ", error);
            reject();
          });
      });
    }
  }
});



