<template>
  <div class="measurements-chart">
    <canvas
      id="measurementsChartCanvas"
      height="250"
    ></canvas>
  </div>
</template>

<script>
export default {
  props: ["data", "scaleMaxHeight", "height", "updateOnValueChange"],
  data: function() {
    return {
      measurementsChart: ""
    };
  },
  watch: {
    updateOnValueChange: function() {
      this.measurementsChart.data.datasets[0].data = this.data[0];
      this.measurementsChart.data.datasets[1].data = this.data[2];
      this.measurementsChart.data.datasets[2].data = this.data[1];
      this.measurementsChart.options.scales.yAxes[0].ticks.max = this.scaleMaxHeight;
      this.measurementsChart.update();
    },
    data: function() {
      this.measurementsChart.data.datasets[0].data = this.data[0];
      this.measurementsChart.data.datasets[1].data = this.data[2];
      this.measurementsChart.data.datasets[2].data = this.data[1];
      this.measurementsChart.options.scales.yAxes[0].ticks.max = this.scaleMaxHeight;
      this.measurementsChart.update();
    }
  },
  mounted: function() {
    const measurementsChartCanvas = document.querySelector(
      "#measurementsChartCanvas"
    );
    this.measurementsChart = new Chart(measurementsChartCanvas, {
      type: "line",
      responsive: true,
      data: {
        labels: ["125 Hz", "250 Hz", "500 Hz", "1000 Hz", "2000 Hz", "4000 Hz"],
        datasets: [
          {
            label: "Advieswaarde",
            data: this.data[0],
            backgroundColor: "rgba(0, 0, 0, 0)",
            borderColor: "#00953e",
            borderWidth: 2,
            datalabels: {
              color: "#00953e",
              anchor: "center",
              align: "start"
            }
          },
          {
            label: "Huidig",
            data: this.data[2],
            backgroundColor: "rgba(0, 0, 0, 0)",
            borderColor: "#0076a6",
            borderWidth: 2,
            datalabels: {
              color: "#0076a6",
              display: true
            }
          },
          {
            label: "Zonder product",
            data: this.data[1],
            backgroundColor: "rgba(0,0,0,0)",
            borderColor: "#cd1e1a",
            borderWidth: 2,
            datalabels: {
              color: "#cd1e1a"
            }
          }
        ]
      },
      options: {
        legend: {
          display: false
        },
        plugins: {
          datalabels: {
            font: {
              weight: "bold"
            },
            anchor: "center",
            align: "end",
            display: "auto",
            formatter: function(value, context) {
              return value.toFixed(2);
            }
          }
        },
        scales: {
          yAxes: [
            {
              ticks: {
                beginAtZero: true,
                max: this.scaleMaxHeight,
                callback: function(value, index, values) {
                  return value.toFixed(2);
                }
              }
            }
          ]
        }
      }
    });
  }
};
</script>



