<template>
  <div class="modal-box">
    <div class="modal-title">
      <h3>
        <base-icon
          iconRef="cube"
          position="left"
        />Nieuwe Ruimte
      </h3>
      <div class="flex-divider"></div>
      <base-button
        type="secondary"
        @click="closeModal"
      >
        <base-icon iconRef="close" />
      </base-button>
    </div>
    <div class="modal-content">
      <div class="modal-content-section">
        <div class="input-header">
          <h4>Basisgegevens</h4>
        </div>
        <div class="input-container">
          <label
            class="label"
            for
          >Ruimtenaam</label>
          <base-input
            type="text"
            placeholder="Ruimtenaam"
            v-model="newRoomData.roomName"
          />
        </div>
        <div class="input-container">
          <label
            for
            class="label"
          >Beschrijving</label>
          <textarea
            class="input"
            rows="4"
            placeholder="Beschrijving"
            v-model="newRoomData.description"
          ></textarea>
        </div>
      </div>
      <div class="modal-content-section">
        <div class="input-header">
          <h4>Eigenschappen</h4>
        </div>
        <div class="input-container">
          <label class="label">Ruimtesoort</label>
          <select
            class="input"
            v-bind:value="newRoomData.type"
            @change="newRoomData.type = $event.target.value"
          >
            <option
              v-for="option in roomTypeOptions"
              :key="option.value"
              :value="option.value"
            >{{option.label}}</option>
          </select>
        </div>
        <div class="input-row">
          <div class="input-container">
            <label class="label">Breedte (m)</label>
            <base-input
              type="number"
              placeholder="0"
              v-model="newRoomData.width"
            />
          </div>
          <div class="input-container">
            <label class="label">Lengte (m)</label>
            <base-input
              type="number"
              placeholder="0"
              v-model="newRoomData.length"
            />
          </div>
          <div class="input-container">
            <label class="label">Hoogte (m)</label>
            <base-input
              type="number"
              placeholder="0"
              v-model="newRoomData.height"
            />
          </div>
        </div>
        <div class="input-row">
          <div class="input-container">
            <label class="label">Aantal personen</label>
            <base-input
              type="number"
              placeholder="0"
              v-model="newRoomData.people.amount"
            />
          </div>
          <div class="input-container">
            <label class="label">Gedrag personen</label>
            <select
              class="input"
              v-bind:value="newRoomData.people.type"
              @change="newRoomData.people.type = $event.target.value"
            >
              <option value="0">Stil aanwezig</option>
              <option value="1">Sprekend</option>
            </select>
          </div>
        </div>
        <div class="input-row">
          <div class="input-container">
            <label class="label">Interieur</label>
            <select
              class="input"
              v-bind:value="newRoomData.interior"
              @change="newRoomData.interior = $event.target.value"
            >
              <option
                v-for="option in $interiors"
                :key="option.value"
                :value="option.value"
              >{{option.label}}</option>
            </select>
          </div>
          <div class="input-container">
            <label class="label">Extra wensen</label>
            <select
              class="input"
              v-bind:value="newRoomData.wishes"
              @change="newRoomData.wishes = $event.target.value"
            >
              <option
                v-for="option in $wishes"
                :key="option.value"
                :value="option.value"
              >{{option.label}}</option>
            </select>
          </div>
        </div>
      </div>
    </div>
    <div class="modal-footer">
      <div class="flex-divider"></div>
      <base-button
        @click="closeModal"
        type="secondary"
      >Annuleren</base-button>
      <base-button
        @click="createRoom"
        :loading="creatingRoom"
        :disabled="creatingRoom"
      >
        <base-icon
          iconRef="checkmark"
          position="left"
        />Aanmaken
      </base-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "room-create-modal",
  data: function() {
    return {
      creatingRoom: false,
      newRoomData: {
        roomName: "",
        description: "",
        height: "",
        length: "",
        width: "",
        type: "",
        interior: "",
        people: {
          type: "",
          amount: ""
        },
        wishes: ""
      }
    };
  },
  computed: {
    roomTypeOptions: function() {
      return this.$advicevalues.getList();
    }
  },
  methods: {
    createRoom: function() {
      this.creatingRoom = true;
      const newRoom = this.$createNewRoom(this.newRoomData);
      newRoom.belongsTo = this.$route.params.projectId;
      this.$store.dispatch("createRoom", newRoom).then(() => {
        this.closeModal();
        this.creatingRoom = false;
      });
    },
    closeModal: function() {
      this.$store.commit("setModalShown", {
        dataType: "room",
        modalType: "create",
        shown: false
      });
    }
  }
};
</script>



