<template>
  <div class="acousticsscore-chart">
    <div class="acousticsscore-wrapper">
      <h4>Rivasono Akoestiek Score</h4>
      <div class="boxes">
        <div class="box label-box">
          <div class="score-label">Huidige akoestiek</div>
          <div class="score-label" v-show="!currentScenarioHasNoProducts">Na toepassen producten</div>
        </div>
        <div class="box score-box">
          <div class="score">
            <span class="score-value current">
              <base-counter
                :end="acousticsScore"
                :decimals="1"
                :max="10"
                :noDecimalsIfAtMax="true"
              />
            </span> /
            <span class="advice-value">10</span>
          </div>
          <div class="score" v-show="!currentScenarioHasNoProducts">
            <span class="score-value products">
              <base-counter
                :end="acousticsScoreWithProducts"
                :decimals="1"
                :max="10"
                :noDecimalsIfAtMax="true"
              />
            </span> /
            <span class="advice-value">10</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.acousticsscore-chart {
  padding: 12px 24px;
}
.acousticsscore-wrapper {
  display: flex;
  flex-direction: column;
  border: 1px solid #d9d9d9;
  padding-bottom: 16px;
  border-radius: 6px;
}
h4 {
  margin-top: 32px;
  font-size: 100%;
  text-align: center;
}
.boxes {
  margin-top: 8px;
  display: flex;
  justify-content: center;
  flex: 1 1 100%;
}
.box {
  display: flex;
  flex-direction: column;
  width: 50%;
}
.label-box {
  margin-right: 12px;
  text-align: right;
}
.score-box {
  margin-left: 12px;
}
.score-label,
.score {
  margin-bottom: 16px;
}
.score {
  font-size: 20px;
}
.score-value {
  font-size: 32px;
  font-weight: bold;
}
.current {
  color: darken(#e53935, 10);
}
.products {
  color: darken(#009ad9, 10);
}
.advice-value {
  color: darken(#00c853, 10);
}
</style>

<script>
import CountUp from "countup.js";
export default {
  props: ["acousticsScores", "currentScenarioHasNoProducts"],
  computed: {
    acousticsScore: function() {
      return this.acousticsScores[0];
    },
    acousticsScoreWithProducts: function() {
      return this.acousticsScores[1];
    }
  }
};
</script>



