<template>
  <header class="header">
    <div class="header-container">
      <div class="header-title-container">
        <router-link to="/">
          <h2 class="heading">
            <img
              src="/static/favicons/android-chrome-192x192.png"
              alt="Sonar Logo"
            > Sonar
          </h2>
        </router-link>
      </div>
      <div class="flex-divider"></div>
      <div
        class="account-container dropdown-container"
        v-if="userLoggedIn"
      >
        <base-button
          class="dropdown-label"
          :type="'secondary'"
        >Account
          <base-icon
            iconRef="chevron-down"
            position="right"
          />
        </base-button>
        <ul class="dropdown-list">
          <li
            class="dropdown-item"
            v-if="isCurrentUserAdmin"
          >
            <base-button
              type="secondary"
              @click="toggleAdminMode"
            >
              <base-icon
                iconRef="flash"
                position="left"
                highlight="warning"
                v-show="adminModeOn"
              />
              <base-icon
                iconRef="flash-off"
                position="left"
                v-show="!adminModeOn"
              />
              Admin</base-button>
          </li>
          <li class="dropdown-item">
            <base-button
              type="secondary"
              @click="logOut"
            >
              <base-icon
                iconRef="log-out"
                position="left"
              />Log uit
            </base-button>
          </li>
        </ul>
      </div>
    </div>
  </header>
</template>

<script>
import firebase from "firebase";

export default {
  name: "Navbar",
  computed: {
    userLoggedIn() {
      return this.$store.getters.getUser !== null;
    },
    isCurrentUserAdmin() {
      return this.$store.getters.getCurrentUserAdminStatus;
    },
    adminModeOn() {
      return this.$store.getters.getAdminModeStatus;
    }
  },
  methods: {
    logOut: function() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$store.dispatch("setUser");
          this.$router.replace("login");
        });
    },
    toggleAdminMode: function() {
      this.$store.commit("toggleAdminMode");
    }
  }
};
</script>

<style>
</style>



