<template>
  <div
    class="modal-box"
    v-if="currentProject"
  >
    <div class="modal-title">
      <h3>
        <base-icon
          iconRef="archive"
          position="left"
        />Bewerk project
      </h3>
      <div class="flex-divider"></div>
      <base-button
        type="secondary"
        @click="closeModal"
      >
        <base-icon iconRef="close" />
      </base-button>
    </div>
    <div class="modal-content">
      <div class="modal-content-section">
        <div class="input-header">
          <h4>Basisgegevens</h4>
        </div>
        <div class="input-container">
          <label
            class="label"
            for
          >Projectnaam</label>
          <base-input
            type="text"
            v-model="projectBody.projectName"
          />
        </div>
        <div class="input-container">
          <label
            for
            class="label"
          >Beschrijving</label>
          <textarea
            class="input"
            v-model="projectBody.description"
          ></textarea>
        </div>
      </div>
      <div class="modal-content-section">
        <div class="input-header">
          <h4>Adres</h4>
        </div>
        <div class="input-row">
          <div class="input-container">
            <label class="label">Straat</label>
            <base-input
              type="text"
              v-model="projectBody.address.street"
            />
          </div>
          <div class="input-container">
            <label class="label">Huisnummer</label>
            <base-input
              type="text"
              v-model="projectBody.address.houseNumber"
            />
          </div>
        </div>
        <div class="input-container">
          <label class="label">Stad</label>
          <base-input
            type="email"
            v-model="projectBody.address.city"
          />
        </div>
        <div class="input-container">
          <label class="label">Postcode</label>
          <base-input
            type="tel"
            v-model="projectBody.address.postCode"
          />
        </div>
      </div>
    </div>
    <div class="modal-footer">
      <base-button
        @click="deleteProject"
        type="danger"
        :loading="deletingProject"
        :disabled="deletingProject || editingProject"
      >Verwijderen</base-button>
      <div class="flex-divider"></div>
      <base-button
        @click="closeModal"
        type="secondary"
      >Annuleren</base-button>
      <base-button
        @click="editProject"
        :loading="editingProject"
        :disabled="editingProject || deletingProject"
      >
        <base-icon
          iconRef="checkmark"
          position="left"
        />Bewerk
      </base-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "project-edit-modal",
  data: function() {
    return {
      deletingProject: false,
      editingProject: false,
      projectBody: {
        projectName: "",
        description: "",
        address: {
          street: "",
          houseNumber: "",
          city: "",
          postCode: ""
        }
      }
    };
  },
  computed: {
    currentProject: function() {
      return this.$store.getters.getProjectsList.find(project => {
        return project.id == this.$route.params.projectId;
      });
    },
    currentClient: function() {
      return this.$store.getters.getClientsList.find(client => {
        return client.id == this.currentProject.data().belongsTo;
      });
    }
  },
  methods: {
    editProject: function() {
      this.editingProject = true;
      const projectData = {
        id: this.$route.params.projectId,
        body: this.$editProjectDataObject(this.projectBody)
      };
      this.$store.dispatch("editProject", projectData).then(() => {
        this.editingProject = false;
        this.closeModal();
      });
    },
    deleteProject: function() {
      this.deletingProject = true;
      const projectData = {
        id: this.$route.params.projectId,
        body: this.$deleteProjectDataObject()
      };
      this.$store.dispatch("editProject", projectData).then(() => {
        this.deletingProject = false;
        this.closeModal();
        this.$router.push(`/klant/${this.currentClient.id}`);
      });
    },
    closeModal: function() {
      this.$store.commit("setModalShown", {
        dataType: "project",
        modalType: "edit",
        shown: false
      });
    }
  },
  mounted: function() {
    this.projectBody = Object.assign({}, this.currentProject.data());
  }
};
</script>



